.navbar {
  height: 80px;
  & img {
    object-fit: contain;
    height: 20px;
  }

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .item {
    display: flex;
    align-items: center;
    font-size: 18px;
  }

  &__left {
    display: flex;
    align-items: center;
    gap: 25px;
  }
  &__center {
    font-size: 30px;
    letter-spacing: 2px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 25px;

    .icons {
      display: flex;
      gap: 15px;
      color: #777;
      cursor: pointer;

      .cartIcon {
        position: relative;

        span {
          font-size: 12px;
          width: 20px;
          height: 20px;
          border-radius: 50%;
          background-color: #2879fe;
          color: white;
          position: absolute;
          right: -10px;
          top: -10px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }
}
